import React from "react";

function About() {
  return(
    <>
      <h1>About</h1>
      <p>您好，这是一个很好用的在线图床网站。</p>
      <p>设计初衷是为了解决图片的存放问题，可以帮助前端工程师在做项目时的图片引用问题。</p>
      <p>通过注册你可以拥有自己的私人存放空间,注册账号主要为了保护您的隐私，不用担心图片泄露的问题。同时你也可以通过 上传历史来查看自己的存放记录，方便您再次使用。</p>

    </>
  )

}

export default About